import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthService } from './../../SERVICES/auth.service';
import { BeneTabsPage } from './bene-tabs.page';

const routes: Routes = [
  {
    path: '',
    component: BeneTabsPage,
    children: [
      {
        path: 'bene-map',
        loadChildren: () => import('../bene-map/bene-map.module').then( m => m.BeneMapPageModule),
        canActivate: [AuthService]
      },
      {
        path: 'bene-list',
        loadChildren: () => import('../bene-list/bene-list.module').then(m => m.BeneListPageModule),
        canActivate: [AuthService]
      },
      {
        path: 'bene-my-events',
        loadChildren: () => import('../bene-my-events/bene-my-events.module').then(m => m.BeneMyEventsPageModule),
        canActivate: [AuthService]
      },
      {
        path: 'bene-compte',
        loadChildren: () => import('../bene-compte/bene-compte.module').then(m => m.BeneComptePageModule),
        canActivate: [AuthService]
      },  
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class BeneTabsPageRoutingModule {}
